@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);

.App{
  width: 100vw;
  height: 100vh;
  /* background: rgb(243, 243, 243); */
  background:rgb(151, 255, 255);
  overflow-x: hidden;
  font-family: Poppins;
}
/* scrollbar */
.App::-webkit-scrollbar{
  /* not to display the scrollbar in the UI */
  display: none; 
}

/* header styles starts */

header{
  background: blueviolet;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 15px;
  height: 60px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  box-shadow: 0px 3px 20px rgba(0,0,0,0.3);
}

.logo{
  min-width: 40px;
  width: 50px;
  border-radius: 10px;
}

.inputBox{
  background: white;
  border-radius: 25px;
  display: flex;
  align-items: center;
  overflow: hidden;
  padding: 8px 15px;
  box-shadow: 0px 3px 20px rgba(0,0,0,0.3);
  width: 120px;
}

.inputBox .searchIcon{
  font-size: 20px;
}

.inputBox input{
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  background: transparent;
  padding-left:8px;
  font-size: 14px;
  font-weight: 500;
}

.shoppingCart{
  position: relative;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
}

.shoppingCart .cart{
  font-size: 30px;
}

.shoppingCart .cart_content{
  width: 20px;
  height: 20px;
  background: rgb(255, 58, 58);
  border-radius: 100%;
  min-width: 20px;
  min-height: 20px;
  position: absolute;
  top: -8px;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shoppingCart .cart_content p{
  color: white;
  font-weight: 500;
}

.profileContainer{
  display: flex;
  align-items: center;
}

.profileContainer .imgBox{
  width: 40px;
  height: 40px;
  min-width: 40px;
  border-radius: 100vw;
  background:cyan;
  display: flex;
  align-items: center;
  justify-content: center;
  /* overflow: hidden; */
}

.profileContainer .imgBox .profilePic{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profileContainer .userName{
  font-size: 16px;
  margin-left: 8px;
  font-weight: 500;
  display: none;
  align-items: center;
}

.toggleMenu{
  transform: rotate(90deg);
}

.toggleMenu .toggleIcon{
  color: yellow;
  font-size: 30px;
}

/* header styles ends*/
 
/* bottom menu styles starts*/

.bottomMenu{
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  background: blueviolet;
  border-radius: 25px 25px 0 0 ;
  z-index: 101;
}

.bottomMenu ul{
  display: flex;
}

.bottomMenu ul li{
  position: relative;
  width: 60px;
  height: 60px;
  z-index: 1;
}

.bottomMenu ul li a{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
  text-align: center;
  font-weight: 500;
}

.bottomMenu ul li a .icon{
  position: relative;
  display: block;
  line-height: 70px;
  text-align: center;
  transition: 0.5s;
}

.bottomMenu ul li a .icon .MuiSvgIcon-root{
  font-size: 25px;
  color: rgb(29, 29, 29);
}

.bottomMenu ul li.active a .icon{
  transform: translateY(-25px);
}

.bottomMenu ul li.active a .icon .MuiSvgIcon-root{
  /* color: black; */
  /* color: white; */
  color: yellow;
  transition: 0.5s;
}

.indicator{
  position: absolute;
  height: 60px;
  width: 60px;
  background: rgb(255, 103, 47);
  border-radius: 100vw;
  top: -50%;
  /* border: 6px solid cyan; */
  border: 6px solid rgb(151, 255, 255);
  transition: 0.5s;
}

.indicator::before{
  content: "";
  position: absolute;
  top: 50%;
  left: -20px;
  width: 18px;
  height: 18px;
  background:transparent;
  border-top-right-radius: 18px;
  /* box-shadow: 0px -10px 0 0 cyan; */
  box-shadow: 0px -10px 0 0 rgb(151, 255, 255);
}

.indicator::after{
  content: "";
  position: absolute;
  top: 50%;
  right: -20px;
  width: 18px;
  height: 18px;
  background:transparent;
  border-top-left-radius: 18px;
  /* box-shadow: 0px -10px 0 0 cyan; */
  box-shadow: 0px -10px 0 0 rgb(151, 255, 255);
}

.bottomMenu ul li:nth-child(1).active ~ .indicator{
  transform: translateX(calc(60px * 0));
}
.bottomMenu ul li:nth-child(2).active ~ .indicator{
  transform: translateX(calc(60px * 1));
}
.bottomMenu ul li:nth-child(3).active ~ .indicator{
  transform: translateX(calc(60px * 2));
}
.bottomMenu ul li:nth-child(4).active ~ .indicator{
  transform: translateX(calc(60px * 3));
}
.bottomMenu ul li:nth-child(5).active ~ .indicator{
  transform: translateX(calc(60px * 4));
}
.bottomMenu ul li:nth-child(6).active ~ .indicator{
  transform: translateX(calc(60px * 5));
}

/* bottom menu styles ends*/

/* main section style starts */

main{
  width: 100%;
  /* if we add content the hieght will adjust according to it */
  height: auto;
  background: cyan;
  /* reducing from header hieght ie 100main hieght - header hieght60px */
  margin-top: 60px;
}

.mainContainer{
  width: 100%;
  height: 140px;
  background:orange;
  padding: 10px;
  padding-bottom: 100px;
}

.banner{
  width: 100%;
  height: 130px;
  border-radius: 10px;
  /* background: url(https://avatars.dicebear.com/api/jdenticon/51561.svg)no-repeat right; */
  background-size: cover;
  position: relative;
  display: flex;
  align-items: center;
  padding: 0px 10px;
}

.deliveryPic{
  position: absolute;
  right: -60px;
  bottom: -10px;
  width: 200px;
}

.bannerContent{
  padding: 8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.bannerContent h3{
  font-size: 18px;
  font-weight: 500;
}

.bannerContent p{
  font-size: 14px;
  width: 200px;
  margin: 5px 0;
}

.bannerContent p span{
  font-weight: 500;
}

.bannerContent a{
  background:white ;
  padding: 5px 15px;
  border-radius: 100vw;
  font-size:14px;
  font-weight: 500;
  cursor: pointer;
}

.bannerContent a:hover{
  box-shadow: 0px 5px 20px rgba(0,0,0,0.15);
  transition: 0.5s;
}

/* dish container starts */

.dishContainer{
  width: 100%;
  padding: 10px 0;
}

.menuCard{
  width: 100%;
}

.subMenuContainer, .viewAll{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.subMenuContainer h3{
  font-size: 16px;
  font-weight: 600;
}

.subMenuContainer.viewAll p{
  font-size: 12px;
  font-weight: 400;
}

.subMenuContainer .viewAll i{
  /* background: white; */
  background: orange;
  width: 20px;
  height: 20px;
  min-width: 20px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}

.subMenuContainer .viewAll i .MuiSvgIcon-root{
  font-size: 16px;
  /* color:orangered; */
  color:white;
}

.rowContainer{
  display: flex;
  align-items: center;
  overflow-x: scroll;
  overflow-y: hidden;
  margin: 10px 0px;
  padding: 10px 0px;
}

.rowContainer::-webkit-scrollbar{
  display: none;
}

.rowMenuCard{
  width: 80px;
  min-width: 80px;
  height: 100%;
  border-radius: 10px;
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 5px;
  box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
  transition: 0.5s;
  cursor: pointer;
  margin-right: 12px;
}

.rowMenuCard:hover, .rowMenuCard.active{
  background: orange;
}

.rowMenuCard .imgBox{
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
  border-radius: 100vw;
  padding: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  overflow: hidden;
}

.rowMenuCard:hover .imgBox, .rowMenuCard.active .imgBox{
  /* background: orange; */
}

.rowMenuCard .imgBox img{
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.rowMenuCard:hover img, .rowMenuCard.active img{
  width: 80%;
  height: 80%;
  transition: 0.5s;
}

.rowMenuCard:hover h3, .rowMenuCard.active h3{
  color: white;
}

.rowMenuCard h3{
  color: orangered;
  font-size: 14px;
  font-weight: 500;
  transition: 0.5s;
}

.rowMenuCard .loadMenu{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  background: orange;
  transition: 0.5s;
}

.rowMenuCard:hover .loadMenu, .rowMenuCard.active .loadMenu{
  background: white;
}

.rowMenuCard .loadMenu .MuiSvgIcon-root{
  font-size: 18px;
  color: white;
}

.rowMenuCard:hover .MuiSvgIcon-root, .rowMenuCard.active .MuiSvgIcon-root{
  color: orange;
}

.dishitemContainer{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.itemCard{
  width: 160px;
  height: 180px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  margin: 10px 0;
}

.isFavourite{
  position: absolute;
  top: 80px;
  right: 10px;
  transition: 0.5s;
}

.isFavourite .MuiSvgIcon-root{
  font-size: 18px;
  color: #ff000088;
}

.isFavourite.active .MuiSvgIcon-root{
  color: #ff0000;
  filter: drop-shadow(0px 0px 8px #ff0000);
}

.itemCard .imgBox{
  width: 100px;
  height: 100px;
  border-radius: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: white; */
  /* padding: 5px; */
  position: absolute;
  top: -10px;
}

.itemCard .imgBox img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.itemImg{
  position: absolute;
  top: 0;
  width: 60%;
}

.itemContent{
  background: white;
  box-shadow: 0px 0px 20px rgba(0,0,0,0.15);
  border-radius: 15px;
  padding: 10px;
  padding-top: 50px;
  width: 100%;
}

.itemContent .itemName{
  font-size: 16px;
}

.itemContent .bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.itemContent .bottom .rating .MuiSvgIcon-root{
  font-size: 16px;
}

.orange{
  color: #f8901c !important;
}

.gray{
  color: #373848 !important;
}

.itemContent .bottom .price{
  font-size: 16px;
}

.itemCard .bottom .price span{
  color: #f8901c;
  font-size: 12px;
}

.itemContent .addtoCart{
  background: #f8901c;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 20px rgba(0,0,0,0.15);
  cursor: pointer;
}

.itemContent .addtoCart .MuiSvgIcon-root{
  color: white;
  font-size: 16px;
}
/* right menu styles */
.rightMenu{
  width: 350px;
  height: calc(100% - 60px);
  background: rgb(255, 217, 223);
  position: fixed;
  right: 0;
  top: 60px;
  z-index: 105;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 10px;
  transform: translateX(200%);
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0px 3px 20px rgba(0,0,0,0.1);
}

.rightMenu.active{
  transform: translateX(0%); 
}
.itemContent.active{
  background: red;
}
/* dish container ends */

/* main section style ends */

.debitCardContainer{
  padding: 10px;
  width: 100%;
  height: auto;
}

.debitCard{
  width: 100%;
  position: relative;
  height: 170px;
  min-width: 300px;
  min-height: 170px;
  border-radius: 15px;
}

.debitCard::before{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f8901c;
  transform: rotate(5deg);
  border-radius: 15px;
  filter: blur(2px);
  z-index: -2;
}

.debitCard::after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(35deg, rgba(255,255,255,0.3), rgba(255,255.255.0.1));
  backdrop-filter: blur(10px);
  z-index: -1;
  border: 1px solid rgba(255,255,255,0.4);
  border-radius: 15px;
}

.cardGroup{
position: relative;
overflow: hidden;
width: 100%;
height: 100%;
}

.card_logo, .card_chip, .card_number, .card_from, .card_to, .card_ring, .card_name{
  position: absolute;
}

.card_logo{
  width: 60px;
  top: 30px;
  right: 30px;
  height: auto;
}

.card_chip{
  width: 40px;
  height: auto;
  top: 50px;
  left: 30px;
  background:white;
  border-radius: 10px;
}

.card_number, .card_from, .card_to, .card_name{
  color: white;
  font: wieght 400;
  letter-spacing: 2px;
}

.card_number{
  left: 30px;
  bottom: 60px;
}

.card_name{
  font-size: 12px;
  bottom: 30px;
  left: 30px;
}

.card_from{
  font-size: 12px;
  bottom: 30px;
  right: 90px;
}

.card_to{
  bottom: 30px;
  right: 30px;
  font-size: 12px;
}

.card_ring{
  height: 400px;
  width: 400px;
  border-radius: 100vw;
  background: transparent;
  border: 50px solid rgba(0,0,0,0.1);
  top: -250px;
  right: -250px;
  box-sizing: border-box;
}

.card_ring::after{
  position: absolute;
  content: "";
  height: 500px;
  width: 500px;
  border-radius: 100vw;
  background: transparent;
  border: 50px solid rgba(0,0,0,0.1);
  top: 185px;
  right: -155px;
  box-sizing: border-box;
}

.cartCheckOutContainer{
  width: 100%;
  min-width: 320px;
}

.cartContainer{
  width: 100%;
  min-height: 250px;
  max-height: 280px;
  margin-top: 10px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.cartContainer::-webkit-scrollbar{
  display: none;
}

.cardItem{
  display: flex;
  align-items: center;
  margin: 8px 0;
}

.cardItem .imgBox{
  width: 60px;
  min-width: 60px;
  height: 60px;
  min-height: 60px;
  background: #00ffc855;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
}

.cardItem .imgBox img{
  width: 100%;
  hieght: 100%;
  object-fit: contain;
}

.cardItem .itemSection{
  margin-left: 10px;
}

.cardItem .itemSection .itemName{
  font-size: 16px;
  color: #2a2b3c;
}

.cardItem .itemSection .itemQuantity{
  font-size: 16px;
  color: #2a2b3c;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 120px;
}

.cardItem .itemSection .itemQuantity span{
  color: #f8901c;
}

.cardItem .itemSection .itemQuantity .quantity{
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 60px;
}

.cardItem .itemSection .itemQuantity .quantity .itemRemove,
.cardItem .itemSection .itemQuantity .quantity .itemAdd{
  color: #2a2b3c;
  font-size: 24px;
}

.cardItem .itemPrice{
  font-size: 16px;
  font-weight: 600;
  color: #2a2b3c;
  margin-left: auto;
}

.cardItem .itemPrice .dolorSign{
  font-size: 12px;
  color: #f8901c;
}

.cardItem .itemPrice .itemPriceValue{
  font-size: 16px;
  color: #373848;
}

.totalSection{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 8px;
  margin: 15px 0px;
}

.totalSection h3{
  font: size 18px;
  font-weight: 600;
  color: #2a2b2c;
}

.totalSection p{
  font-size: 18px;
  font-weight: 600;
  color: #2a2b3c;
}

.totalSection p span{
  font-size: 14px;
  color: #f8901c;
}

.checkOut{
  outline: none;
  border: none;
  width: 100%;
  padding: 10px 20px;
  background: #f8901c;
  border-radius: 100vw;
  color: #faf9fb;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 2px;
}


@media screen and (min-width: 650px) {
  .rowContainer {
    justify-content: space-between;
  }

  .inputBox {
    width: 60%;
  }
}

@media screen and (min-width: 950px) {
  .toggleMenu {
    display: none;
  }

  .profileContainer .userName {
    display: block;
  }

  .leftMenu {
    left: 21%;
    width: 40%;
  }

  .mainContainer {
    width: calc(100% - 350px);
  }
  .banner {
    height: 160px;
  }

  .deliveryPic {
    bottom: -13px;
    width: 278px;
  }

  .bannerContent h3 {
    font-size: 24px;
  }

  .bannerContent p {
    font-size: 15px;
    margin: 12px 0;
    width: unset;
  }

  .bannerContent a {
    font-size: 16px;
  }

  main {
    display: flex;
  }
  .mainContainer {
    padding: 10px 30px;
  }
  .rightMenu {
    position: fixed;
    z-index: 1;
    transform: translateX(0%);
    top: unset;
    padding: 20px 15px;
  }
  .itemCard {
    width: 180px;
    height: 200px;
  }
}






